import { useEffect, useState } from "react";
import "styles/app.scss";
import * as contractUtils from "utils/ContractUtil";

function importAll(r) {
	let images = {};
	r.keys().map((item, _) => {
		images[item.replace("./", "")] = r(item);
	});
	return images;
}

const assets = importAll(
	require.context("assets", false, /\.(png|jpe?g|svg)$/)
);

function App() {
	const [currAccount, setCurrAccount] = useState(null);

	const contractButton = () => {
		return (
			<button
				onClick={() => !currAccount ? setCurrAccount(contractUtils.connectWallet) : contractUtils.mint}
				className="contractButton"
			>
				{!currAccount ? "Connect Wallet" : "Mint NFT"}
			</button>
		);
	};

	useEffect(() => {
		setCurrAccount(contractUtils.checkWallet());
	}, []);

	return (
		<div class="content">
			<div class="titleContent">
				<h1>BlazinBuffaloes</h1>
				<h2>Stampeding Soon</h2>
			</div>
			<img class="logo" src={assets["blazinbuffalo.svg"]} alt="BlazinBuffalo NFT Graphic"></img>
			{/* <div>{contractButton()}</div> */}
		</div>
	);
}

export default App;
